<template>
  <div>
    <Row>
      <Col :span="19">
        <Button icon="ios-plus-empty" type="dashed" size="small" @click="addTag">添加分类</Button>
        <Tag v-for="(item,index) in comboxes" :key="item.id" type="dot" closable :color="colors[index%4]" @on-close="removeTag(item.id)" @click.native="modifyTag(item)">{{item.name}}</Tag>
      </Col>
      <Col :span="5" style="position:relative;">
      <Card :class="active?'hint-wrap':'hint-wrap'">
        <Icon type="navicon-round" class="hint-expend" @click.native="active=!active"></Icon>
        <p>1.点击 <span class="wrong-txt">‘添加分类’</span> ，新增一个分类；</p>
        <p>2.点击 <span class="wrong-txt">分类名称</span>，即可修改此分类；</p>
        <p>2.点击分类右侧的 <span class="wrong-txt">删除</span> 按钮，即可删除该分类</p>
      </Card>
      </Col>
    </Row>

     <Modal class="modal" v-model="deleteClassify" width="360">
        <p slot="header" style="color:#f60;text-align:center">
            <Icon type="ios-information-circle"></Icon>
            <span>删除提醒</span>
        </p>
        <div style="text-align:center">
            <p>删除分类会使得已经分配该分类的指标失去这个分类标签,</p>
            <p>是否继续？</p>
        </div>
        <div slot="footer">
            <Button type="error" size="large" long @click="deleteClass">删除</Button>
        </div>
    </Modal>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        comboxes: [],
        colors: ['blue', 'green', 'red', 'yellow'],
        name: '',
        id: '',
        active: false,
        deleteClassify: false,
        deleteId: ''
      }
    },
    created() {
      this.getComboxList();
    },
    methods: {
      modifyTag(item) {
        this.modify = true;
        this.name = item.name;
        this.id = item.id;
        this.handleAdd();
      },
      addTag() {
        this.name = '';
        this.id = '';
        this.handleAdd();
      },
      getComboxList() {
        this.$ajaxPost(this.projectName + '/mgmt/group/finds.do', {}).then(res => {
          // console.log(res)
          this.comboxes = res;
        })
      },
      removeTag(id) {
        this.deleteClassify = true
        this.deleteId = id
      },
      deleteClass() {
        this.$ajaxPost(this.projectName + '/mgmt/group/delete.do', {
          id: this.deleteId
        }).then(res => {
          this.$Message.success('删除成功')
          this.deleteClassify = false
          this.getComboxList()
        })
      },
      addSubmit() {
        this.$ajaxPost(this.projectName + '/mgmt/group/save.do', {
          groupName: this.name
        }).then(res => {
          this.$Message.success('恭喜你，新增分类成功!');
          this.getComboxList();
        });
      },
      modifySubmit() {
        this.$ajaxPost(this.projectName + '/mgmt/group/update.do', {
          groupName: this.name,
          id: this.id
        }).then(res => {
          this.$Message.success('恭喜你，分类修改成功!');
          this.getComboxList();
        });
      },
      handleSubmit() {
        if (!!this.id) {
          this.modifySubmit();
        } else {
          this.addSubmit();
        }
      },
      handleAdd() {
        this.$Modal.confirm({
          render: (h) => {
            return h('Input', {
              props: {
                value: this.name,
                autofocus: true,
                placeholder: '请输入分类名称...'
              },
              on: {
                input: (val) => {
                  this.name = val;
                }
              }
            })
          },
          onOk: () => {
            this.handleSubmit();
          },
          onCancel: () => {
            this.name = '';
          },
        })
      },
    }
  }
</script>

<style lang="scss" scoped>
  .hint-wrap {
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    transition: all 1s;
    &-expand {
      width: 48px;
      height: 46px;
      transition: all 1s;
      position: absolute;
      top: 0;
      right: 0;
      overflow: hidden;
    }
  }
  .hint-expend {
    font-size: 22px;
    position: absolute;
    right: 15px;
    top: 10px;
    cursor: pointer;
  }
  p {
    height: 50px;
    line-height: 50px;
  }
  .wrong-txt {
    color: red;
    font-size: 16px;
    font-weight: 700;
  }
  .modal{
    p{
      line-height: 30px;
      height: 30px;
    }
  }
</style>



