<template>
  <div class="time-manage">
    <el-card class="box-card">
      <el-form :model="form">
        <el-form-item label="设置时间：" label-width="120px">
          <el-date-picker
            v-model="form.startTime"
            type="datetime"
            placeholder="选择开始时间"
            @change='changeTimeStart'>
          </el-date-picker>&nbsp;&nbsp;至&nbsp;
          <el-date-picker
            v-model="form.endTime"
            type="datetime"
            placeholder="选择结束时间"
            @change='changeTimeEnd'>
          </el-date-picker>
        </el-form-item>
        <el-form-item label-width="120px">
          <el-switch
            v-model="form.status"
            active-color="#13ce66"
            inactive-color="#ff4949"
            active-value="1"
            inactive-value="2"
            active-text="启用"
            inactive-text="禁用">
          </el-switch>
        </el-form-item>
      </el-form>
     <p class="editor-btn"><el-button type="primary" size="small" @click="editorTime">保存</el-button></p>
    </el-card>
    
    <el-card class="card-tips" shadow="always">
      <div class="tip-title">温馨提示</div>
      时间管理用于管控指标数据的录入时间，若启用该配置，则在规定时间以外，任何人均无法录入和修改指标数据。
    </el-card>

  </div>
</template>

<script>
import moment from 'moment'
export default {
  data() {
    return {
      form: {
        id: null,
        startTime: '',
        endTime: '',
        status: '1'
      }
    }
  },
  created() {
    this.getSetOverTime()
  },
  methods: {
    async getSetOverTime() {
      this.time = []
      const res = await this.$ajaxPost(this.projectName + '/mgmt/openTime/find.do')
      // console.log(res)
      this.form.startTime = res.startTime
      this.form.endTime = res.endTime
      this.statusFlag = res.status
      this.form.id = res.id
    },
    changeTimeStart(val) {
      this.form.startTime = moment(val).format('YYYY-MM-DD HH:mm:ss')
    },
    changeTimeEnd(val) {
      this.form.endTime = moment(val).format('YYYY-MM-DD HH:mm:ss')
    },

    async editorTime() {
      if(this.form.startTime>this.form.endTime){
        let temp = this.form.startTime
        this.form.startTime = this.form.endTime
        this.form.endTime = temp
      }
      const res = await this.$ajaxPost(this.projectName + '/mgmt/openTime/update.do', this.form)
      // console.log(res)
      this.$Message.success(res)
      this.getSetOverTime()
    }
  }
}
</script>

<style lang="scss">
.time-manage{
  .box-card{
    width: 50%;
    float: left;
    p{
      font-size: 15px;
      margin-bottom: 10px;
      &:last-child{
        margin-bottom: 0;
      }
      &.editor-btn{
        text-align: right;
      }
    }
  }
  .card-tips{
    float: right;
    width: 400px;
    font-size: 14px;
    .tip-title{
      color: #f65c5c;
      margin-bottom: 10px;
    }
  }
}
</style>



