<template>
  <div class="index-list-template">
    <Row class="index-show-content">
      <Col span="4" class="index-tree">
        <Tree :data="dataTree" @on-select-change="clickTreeNode"></Tree>
      </Col>
      <Col span="20" class="index-table">
        <!-- 搜索部分 -->
        <query-form ref="queryForm" @initList="SearchListData" @listData="searchResult" @queryResult="queryResult"></query-form>
        <!-- table数据列表 -->
        <Table ref="selection" :columns="columns" :data="list" :loading="loading" :no-data-text="textTips" @on-selection-change="selectMethod">
          <Button type="ghost" slot="header" class="m-marlr" @click="addRow">新增</Button>
          <Button type="ghost" slot="header" @click="csvRows">导出</Button>
        </Table>
        <!-- 分页 -->
        <Page :total="total" show-total @on-change="pageChange" class="page"></Page>
      </Col>
    </Row>

    <!-- 添加或修改指标 -->
    <add-from :isAdd="isAdd" :modify="ismodify" :parentName="parentName" @cancelAdd="cancelAdd" :formValidate="editList" @getList="loadListData"></add-from>

  </div>
</template>

<script>
  import queryForm from './components/query.vue'
  import addFrom from './components/add.vue'
  import iView from 'iview';
  import _ from 'lodash';
  export default {
    components: {
      queryForm,
      addFrom
    },
    data() {
      return {
        yearList: [],
        isSetData: false,
        selectedObject: {},
        isAdd: false,
        ismodify: false,
        editList: {},
        list: [],
        loading: false,
        setYears: [],
        columns: [
          {
            title: '层级',
            key: 'nodeIds',
            width: 120,
            render: (h, params) => {
              return h('div', [
                h('span', params.row.nodeIds.split(',').length === 1 ? '一级指标' : (params.row.nodeIds.split(',').length === 2 ? '二级指标' : '三级指标') )
              ])
            }
          },
          {
            title: '名称',
            key: 'name'
          },
          {
            title: '父级',
            key: 'parentId',
            render: (h, params) => {
              return h('div', [
                h('span', !(+params.row.parentId) ? '无' : params.row.parentName)
              ])
            }
          },
          {
            title: '指标总分',
            key: 'grossScore',
            width: 100,
          },
          {
            title: '分类',
            key: 'indexGroups',
            render: (h, params) => {
              return h('div', [
                h('span', params.row.indexGroups.map(item => item.groupName).join(','))
              ])
            }
          },
          {
            title: '单位',
            key: 'unitsName',
            width: 80,
          },
          {
            title: '状态',
            key: 'indexStatus',
            width: 120,
            render: (h, params) => {
              const TagRow = h('span', {
                style: {
                  color: (+params.row.indexStatus === 1) ? '#67c23a' : '#f56c6c'
                }
              }, (+params.row.indexStatus === 1) ? '有效指标' : '无效指标')
              return h('div', [TagRow]);
            }
          },
          {
            title: '操作',
            key: 'action',
            align: 'center',
            render: (h, params) => {
              const modifyInfo = h('Button', {
                props: {
                  type: 'primary',
                  size: 'small'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.editRow(params)
                  }
                }
              }, '修改')
              return h('div', [modifyInfo]);
            }
          }
        ],
        params: {
          pageNumber: 1,
          pageSize: 10
        },
        total: 0,
        dataTree: [],
        canClick: false,
        nodeInfo: [],
        selectParentId: 0,
        parentName: '',
        searchInfo: {},
        levelIndex: '',
        textTips: '没有符合条件的指标',
        isQuery: false,
        selectIndexStatus: ''
      }
    },
    created() {
      this.getParentListData()
    },
    methods: {
      // 点击指标树节点
      async clickTreeNode(node) {
        console.log(node)
        this.canClick = true
        if(node.length === 0) return
        this.selectParentId = node[0].id
        this.selectIndexStatus = node[0].indexStatus
        this.parentName = node[0].name
        this.nodeInfo = node
        this.loading = true
        const res = await this.$ajaxPost(this.projectName + '/mgmt/index/finds.do', {
          parentId: this.selectParentId,
          ...this.params,
          ...this.searchInfo
        })
        this.loading = false
        // console.log('点击指标',res)
        this.list = res.rows
        this.total = res.total
        
        if(node[0].nodeIds) {
          const levelNum = node[0].nodeIds.split(',').length
          this.levelIndex = (levelNum === 1 ? '一' : (levelNum === 2 ? '二' : '三'))
          if(this.total === 0) {
            if(!this.searchInfo.name || !this.searchInfo.indexStatus){
              this.textTips = this.levelIndex + '级指标 - “' + this.parentName + '” 下没有符合的指标'
            }
          }
        }
      },
      // 点击切换管理模块
      changeRadio() {
        this.getParentListData()
        this.list = [] // 列表显示清空
        this.canClick = false // 新增按钮不可点
      },
      // table在多选模式下有效，只要选中项发生变化时就会触发 (*****)
      selectMethod(cs) {
        // console.log(cs)
        if (!(this.params.pageNumber in this.selectedObject)) {
          this.selectedObject[this.params.pageNumber] = []
        }
        this.selectedObject[this.params.pageNumber].splice(0, this.selectedObject[this.params.pageNumber].length, ...cs)
      },
      // 点击生成数据 数据处理function (*****)
      formatSelectedData() {
        const params = []
        for (const k in this.selectedObject) {
          let ids = this.selectedObject[k].map(e => {
            return e.id;
          })
          params.push(...ids)
        }
        return params
      },
     
      // 导出指标列表
      csvRows() {
        const params = this.formatSelectedData();
        window.open(this.projectName + '/mgmt/index/exportExcel.do?locker=6f0c077ee53d4aa68b9eae5b3e853bb8', "_self");
      },
      // 新增指标
      addRow() {
        if(!this.canClick) {
          this.$Message.warning('请选择左侧指标，确定新增的指标所属层级！')
          return
        }
        this.isAdd = true
        this.ismodify = false
        this.editList = {
          name: '',  // 指标名称
          parentId: this.selectParentId, // 指标的父级id
          indexStatus: this.selectIndexStatus === '2' ? '2' : '', //指标状态（1 有效指标  2 无效指标）
          unitsId: '', //计量单位id
          evaluationStandards: '', // 评分标准描述
          description: '', //指标描述
          grossScore: 1, //指标总分
          groupId: []
        }
      },
      // 修改指标
      async editRow(params) {
        // console.log(params)
        const res = params.row
        const editParentId = res.parentId
        if(editParentId === 0) {
          this.editHande(res)
          return
        }
        const result = await this.$ajaxPost(this.projectName + '/mgmt/index/find.do', {
          id: editParentId
        })
        // console.log(result)
        if(result.indexStatus !== '1') {
          this.$Message.warning('该指标的上级指标状态为无效，无法进行修改！')
          return
        }
        this.editHande(res)
      },
      editHande(res) {
        this.isAdd = true
        this.ismodify = true
        this.editList = {
          id: res.id,
          name: res.name,  // 指标名称
          parentId: res.parentId, // 指标的父级id
          indexStatus: res.indexStatus, //指标状态（1 有效指标  2 无效指标）
          unitsId: res.unitsId, //计量单位id
          evaluationStandards: res.evaluationStandards, // 评分标准描述
          description: res.description, //指标描述
          grossScore: res.grossScore, //指标总分
          groupId: res.indexGroups.map(item => item.groupId)
        }
      },
      // 添加或修改数据完成，重新渲染数据，子组件传递过来的function
      loadListData(params) {
        // this.$set(this.nodeInfo[0], 'selected', true)
        this.clickTreeNode(this.nodeInfo)
        this.getParentListData()
        this.params.pageNumber = 1
      },
      // 单独处理 搜索 两个参数的结果
      searchResult(data) {
        // console.log(data)
        this.list = data.rows
        this.total = data.total
      },
      // 搜索组件传值
      SearchListData(params) {
        // console.log(params)
        this.searchInfo = params
      },
      checkedSelected() {
        if (!(this.selectedObject[this.params.pageNumber] && this.selectedObject[this.params.pageNumber].length)) return;
        this.selectedObject[this.params.pageNumber].forEach(e => {
          const tem = _.filter(this.list, _.matches({
            id: e.id
          }))
          if (tem.length) tem[0]._checked = true
        })
      },
      queryResult(data) {
        // console.log(data)
        this.isQuery = data
      },
      // 分页
      async pageChange(page) {
        if(this.isQuery){
          const res = await this.$ajaxPost(this.projectName + '/mgmt/index/finds.do', {
            ...this.searchInfo,
            pageNumber: page,
            pageSize: 10
          })
          this.list = res.rows
          this.total = res.total
        }else{
          this.params.pageNumber = page
          this.loadListData()
        }
        
      },
      // 子组件传过来的function
      cancelAdd() {
        this.isAdd = false
      },
      // 获取指标体系属性图数据
      async getParentListData() {
        const dataTreeInfo = []
        let rootObj = {
          title: '全部指标',
          parentId: 0,
          id: 0,
          expand: true
        }
        const res = await this.$ajaxPost(this.projectName + '/mgmt/index/findAllIndex.do')
        // console.log(res)
        const parentFormatData = res
        parentFormatData.forEach(el => {
          this.$set(el, 'title', el.name)
          // this.$set(el, 'expend', true)
          if(this.nodeInfo.length !== 0){
            if(el.id === this.nodeInfo[0].id){
              this.$set(el, 'selected', true)
              this.$set(el, 'expend', true)
            }
          }          
        })
        // 获得最低层级数
        const indexNumber = parentFormatData.map(item => item.nodeIds.split(',').length).sort().reverse()[0]
        const arr = []
        for(let a = 1; a<=indexNumber; a++){
          arr.push(parentFormatData.filter(item => item.nodeIds.split(',').length === a))
        }
        for(let j = 1; j<arr.length; j++){
          this.handleParentData(arr[j-1], arr[j])
        }
        // console.log(arr[0])
        rootObj.children = arr[0]
        dataTreeInfo.push(rootObj)
        this.dataTree = dataTreeInfo
      },
      // 联级数据处理
      handleParentData(pre, next) {
        let str = ''
        return pre.forEach(ele => {
          next.forEach(k => {
            str = k.nodeIds.split(',').slice(0, k.nodeIds.split(',').length - 1).join(',')
            if(ele.nodeIds === str) {
              if(!ele.children){
                ele.children = []
              }
              ele.children.push(k)
            }
          })
        })
      }
    }
  }
</script>

<style lang="scss">
  .index-list-template{
    border: 1px solid #e9eaec;
    .ivu-radio-group{
      display: block !important;
      height: 50px;
      line-height: 50px;
      box-sizing: border-box;
      padding: 0 10px;
      border-bottom: 1px solid #e9eaec;
      span.text{
        display: inline-block;
        height: 50px;
        line-height: 50px;
        vertical-align: bottom;
      }
      .ivu-radio-wrapper{
        margin: 0 28px 0 10px;
      }
    }
    .index-show-content{
      height: 720px;
      .index-tree{
        height: 100%;
        border-right: 1px solid #e9eaec;
        box-sizing: border-box;
        padding: 10px 20px;
      }
      .index-table{
        box-sizing: border-box;
        padding: 10px 20px;
      }
    }
    
    .page {
      float: right;
      margin: 20px 0;
    }
    .m-marlr {
      margin: 0 16px;
    }
  }
  
</style>



