<template>
  <div class="add-modify-index">
    <Modal :title="!modify?'新增':'修改'" v-model="isAdd" :mask-closable="false" :width="65" :closable="false">
      <Form ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="90">
        <FormItem label="上级指标">
          <Input :placeholder="parentName ? parentName : '无父级指标，本身为一级指标'" disabled></Input>
        </FormItem>
        <FormItem label="指标名称" prop="name">
          <Input v-model.trim="formValidate.name" placeholder="请输入指标名称..."></Input>
        </FormItem>
        <FormItem label="指标描述" prop="description">
          <Input type="textarea" v-model.trim="formValidate.description" placeholder="请输入指标描述..."></Input>
        </FormItem>
        <FormItem label="评分标准" prop="evaluationStandards">
          <Input type="textarea" v-model.trim="formValidate.evaluationStandards" placeholder="请输入评分标准..."></Input>
        </FormItem>
        <FormItem label="指标总分" prop="grossScore">
          <InputNumber :min="1" v-model="formValidate.grossScore"></InputNumber>
        </FormItem>
        <FormItem label="分类" prop="groupId">
          <Select v-model="formValidate.groupId" placeholder="请选择分类..." multiple>
            <Option :value="item.id" v-for="item in groupList" :key="item.id">{{item.name}}</Option>
          </Select>
        </FormItem>
        <FormItem label="计量单位" prop="unitsId">
          <Select v-model="formValidate.unitsId" placeholder="请选择计量单位...">
            <Option :value="item.id" v-for="item in selects" :key="item.id">{{item.unitsName}}</Option>
          </Select>
        </FormItem>
        <FormItem label="状态" prop="indexStatus">
          <Select v-model="formValidate.indexStatus" placeholder="请选择状态..." :disabled="disabledParentId !== 0 && disabledIndexStatus === '2'">
            <Option :value="'1'">有效指标</Option>
            <Option :value="'2'">无效指标</Option>
          </Select>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="primary" @click="handleSubmit('formValidate')">{{!modify?'新增':'修改'}}</Button>
        <Button type="ghost" @click="handleReset" style="margin-left: 10px">取消</Button>
      </div>
    </Modal>
  </div>
</template>
<script>
  import Cookies from 'js-cookie';
  export default {
    props: ['parentName', 'isAdd', 'formValidate', 'modify'],
    data() {
      const validateindexUnitsId = (rule, value, callback) => {
        if (!this.formValidate.unitsId) {
          callback(new Error('请选择单位...'));
        } else {
          callback();
        }
      }
      return {
        selects: [],
        groupList:[],
        disabledIndexStatus: '',
        disabledParentId: -1,
        ruleValidate: {
          name: [{
            required: true,
            message: '请输入标题...',
            trigger: 'blur'
          }],
          description: [{
            required: true,
            message: '请输入指标描述...',
            trigger: 'blur'
          }],
          evaluationStandards: [{
            required: true,
            message: '请输入评分标准...',
            trigger: 'blur'
          }],
          unitsId: {
            required: true,
            validator: validateindexUnitsId,
            trigger: 'change'
          },
          indexStatus: {
            required: true,
            trigger: 'change',
            message: '请选择状态...',
          }
        }
      }
    },
    methods: {
      // 获取单位列表
      async getInstitutionList() {
        const res = await this.$ajaxPost(this.projectName + '/mgmt/units/finds.do')
        this.selects = res
      },
      async getGroupList() {
        const res = await this.$ajaxPost(this.projectName + '/mgmt/group/finds.do')
        this.groupList = res
      },
      resetFormateData() {
        this.handleReset();
        this.$emit('getList');
      },
      addSubmit() {
        this.$ajaxPost(this.projectName + '/mgmt/index/save.do', this.formValidate).then(res => {
          this.$Message.success('恭喜你，新增成功!')
          this.resetFormateData()
        });
      },
      modifySubmit() {
        this.$ajaxPost(this.projectName + '/mgmt/index/update.do', this.formValidate).then(res => {
          this.$Message.success('恭喜你，修改成功!')
          this.resetFormateData()
        });
      },
      // 新增确定按钮
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid){
            // console.log(this.formValidate)
            if (this.modify) {
              this.modifySubmit()
            } else {
              this.addSubmit()
            }
          } else {
            let hit = this.modify ? '修改' : '新增'
            this.$Message.error(hit + '失败，请刷新后重新尝试')
          }
        })
      },
      handleReset(name) {
        this.$emit('cancelAdd')
        this.$refs['formValidate'].resetFields()
      }
    },
    watch: {
      isAdd() {
        if (this.isAdd) {
          this.getInstitutionList()
          this.getGroupList()
          // console.log(this.formValidate)
          this.disabledIndexStatus = this.formValidate.indexStatus
          this.disabledParentId = this.formValidate.parentId
        }
        if (this.isAdd && this.modify) {
          this.disabledIndexStatus = ''
          this.disabledParentId = -1
        }
      }
    }
  }
</script>



