<template>
  <Form ref="formValidate" :model="formValidate" :label-width="60">
    <Row>
      <Col :span="8">
        <FormItem label="名称" prop="name">
          <Input v-model.trim="formValidate.name" placeholder="请输入名称..." @on-blur="inputChange"></Input>
        </FormItem>
      </Col>
      <Col :span="6">
        <FormItem label="状态" prop="indexStatus">
          <Select v-model="formValidate.indexStatus" placeholder="请选择指标状态..." @on-change="selectChange">
            <Option :value="'1'">有效指标</Option>
            <Option :value="'2'">无效指标</Option>
          </Select>
        </FormItem>
      </Col>
      <Col :span="10" style="text-align: right">
        <FormItem>
          <Button type="primary" @click="handleSubmit('formValidate')">确定</Button>
          <Button type="ghost" @click="handleReset" style="margin-left: 10px">重置</Button>
        </FormItem>
      </Col>
    </Row>
  </Form>
</template>
<script>
  export default {
    data() {
      return {
        formValidate: {
          name: '',
          indexStatus: ''
        },
        params: {
          pageNumber: 1,
          pageSize: 10
        }
      }
    },
    methods: {
      handleSubmit(name) {
        // console.log(name)
        this.$emit('initList', this.formValidate)
        if(!this.formValidate.name && !this.formValidate.indexStatus){
          this.$Message.warning('无搜索条件')
          return
        }
        this.$refs[name].validate(async (valid) => {
          if (valid) {
            const res = await this.$ajaxPost(this.projectName + '/mgmt/index/finds.do', {
              ...this.formValidate,
              ...this.params
            })
            // console.log(res)
            this.$emit('listData', res)
            this.$emit('queryResult', true)
          } else {
            this.$Message.error('搜索条件有误');
          }
        })
      },
      handleReset(name) {
        this.$refs['formValidate'].resetFields()
        this.$emit('initList', this.formValidate)
      },
      selectChange() {
        this.$emit('initList', this.formValidate)
      },
      inputChange() {
        this.$emit('initList', this.formValidate)
      }
    }
  }
</script>

<style lang="scss" scoped>
.ivu-form-item{
  margin-bottom: 10px;
}
</style>



