<template>

</template>
<script>
  export default {
    data() {
      return {
        comboxes:[]
      }
    },
    created () {
      this.getComboxList();
    },
    methods: {
      getComboxList() {
        this.$ajaxPost(this.projectName+'/mgmt/city/cityCombox.do', {}).then(res => {
          console.log(res)
          this.comboxes = res;
        })
      }
    }
  }
</script>


