<template>
  <div>
    <Button icon="ios-plus-empty" type="dashed" size="small" @click="addTag">添加单位</Button>
    <Tag v-for="(item,index) in comboxes" :key="item.id" type="dot" closable :color="colors[index%4]" @on-close="removeTag(item.id)" @click.native="modifyTag(item)">{{item.unitsName}}</Tag>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        comboxes: [],
        colors: ['blue', 'green', 'red', 'yellow'],
        unitsName: '',
        id: ''
      }
    },
    created() {
      this.getComboxList();
    },
    methods: {
      modifyTag(item) {
        this.modify = true;
        this.unitsName = item.unitsName;
        this.id = item.id;
        this.handleAdd();
      },
      addTag(){
        this.unitsName = '';
        this.id = '';
        this.handleAdd();
      },
      getComboxList() {
        this.$ajaxPost(this.projectName+'/mgmt/units/finds.do', {}).then(res => {
          this.comboxes = res;
        })
      },
      removeTag(id) {
        this.$ajaxPost(this.projectName+'/mgmt/units/delete.do', {
          id: id
        }).then(res => {
          this.$Message.success('删除成功');
          this.getComboxList();
        })
      },
      addSubmit() {
        this.$ajaxPost(this.projectName+'/mgmt/units/save.do', {
          unitsName: this.unitsName
        }).then(res => {
          this.$Message.success('恭喜你，新增单位成功!');
          this.getComboxList();
        });
      },
      modifySubmit() {
        this.$ajaxPost(this.projectName+'/mgmt/units/update.do', {
          unitsName: this.unitsName,
          id: this.id
        }).then(res => {
          this.$Message.success('恭喜你，单位修改成功!');
          this.getComboxList();
        });
      },
      handleSubmit() {
        if (!!this.id) {
          this.modifySubmit();
        } else {
          this.addSubmit();
        }
      },
      handleAdd() {
        this.$Modal.confirm({
          render: (h) => {
            return h('Input', {
              props: {
                value: this.unitsName,
                autofocus: true,
                placeholder: '请输入单位名称...'
              },
              on: {
                input: (val) => {
                  this.unitsName = val;
                }
              }
            })
          },
          onOk: () => {
            this.handleSubmit();
          },
          onCancel: () => {
            this.unitsName = '';
          },
        })
      },
    }
  }
</script>

<style>

</style>



