<template>
  <div class="department-manage">
    <template v-for="item in comboxes">
      <el-card shadow="hover" :key="item.num">
        <el-tag v-for="tag in item.list" :key="tag.id" @click.native="clickTag(tag, item.num)" :type='tag.typeColor' :checkable="true">
          {{tag.departmentName}}{{tag.checked}}
        </el-tag>
        <el-button icon="el-icon-edit" size="small" @click="modifyDepartment(item)"></el-button>
        <el-button icon="el-icon-plus" size="small" @click="addDepartment(item)"></el-button>
      </el-card>
    </template>

    <Modal :title="modify ? '修改' : '新增'" v-model="modalShow" :mask-closable="false" :width="35" :closable="false">
      <Form ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="70">
        <FormItem label="上级部门">
          <Input :placeholder="num === 0?'无':parentName" disabled></Input>
        </FormItem>
        <FormItem label="部门名称" prop="departmentName">
          <Input v-model="formValidate.departmentName" placeholder="请输入部门名称..." @on-enter="handleSubmit('formValidate')"></Input>
        </FormItem>
        <FormItem label="是否撤销" prop="departmentStatus">
          <i-switch size="large" v-model="formValidate.departmentStatus" :true-value="1" :false-value="2">
            <span slot="open">启用</span>
            <span slot="close">撤销</span>
          </i-switch>
          <span class="hint-txt">(注：撤销后对应部门下的指标均不可见，但不会被删除。)</span>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="primary" @click="handleSubmit('formValidate')">{{modify ? '修改' : '新增'}}</Button>
        <Button type="ghost" @click="handleReset('formValidate')" style="margin-left: 8px">取消</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
export default {
  data() {
    return {
      num: 0,
      comboxes: [{
        num: 0,
        id: 0,
        list: []
      }],
      parentName: '',
      allList: [],
      parentList: [],
      typeColor: '',
      checkedId: null, // 被点击中的标签部门
      modalShow: false,
      formValidate: {
        departmentName: '',
        departmentStatus: 1,
        parentId: 0
      },
      ruleValidate: {
        departmentName: [
          { required: true, message: '输入内容...', trigger: 'blur' }
        ]
      },
      modify: false,
      checkedIdArr: []
    }
  },
  created() {
    this.getDepartmentAllList()
  },
  methods: {
    async getDepartmentAllList() {
      const res = await this.$ajaxPost(this.projectName + '/mgmt/department/findAll.do')
      // console.log(res)
      this.allList = res
      this.allList.forEach((el, index, arr) => {
        // this.$set(el, 'typeColor', this.checkedIdArr.map(item => item.tag.id).includes(el.id)?'success':el.departmentStatus==='1'?'':'info')
        // this.$set(el, 'checked', this.checkedIdArr.map(item => item.tag.id).includes(el.id))
        if(this.checkedIdArr.map(item => item.tag.id).includes(el.id)){
          this.$set(el, 'typeColor', 'success')
          this.$set(el, 'checked', true)
        } else {
          this.$set(el, 'typeColor', el.departmentStatus==='1'?'':'info')
          this.$set(el, 'checked', false)
        }
      })
      // console.log(this.allList)
      // console.log(this.allList.map(item=>item.typeColor))
      // console.log(this.allList.map(item=>item.checked))
      // 首先显示第一行
      this.comboxes[0].list = this.allList.filter(item => item.parentId === 0)
    },
    getChildrenList(number, tempId, tag){
      // 先去出该位置下所有元素，然后重新添加
      this.comboxes.splice(number+1, this.comboxes.length)
      let list = this.allList.filter(item => item.parentId === tempId)
      list.forEach(el => {
        this.$set(el, 'typeColor', el.departmentStatus==='1'?'':'info')
        this.$set(el, 'checked', false)
      })
      this.comboxes[number+1] = {
        num: number+1,
        id: tempId,
        tag: tag,
        list
      }
      // console.log(this.comboxes)
    },
    // 处理被点击的tag数组
    checkedTagList(num, tag) {
      const checkedObj = {}
      this.$set(checkedObj, 'line', num)
      this.$set(checkedObj, 'tag', tag)
      if(this.checkedIdArr.map(item => item.line).includes(num)){
        let existTag = this.checkedIdArr.filter(item => item.line === num)[0]
        this.checkedIdArr.splice(this.checkedIdArr.indexOf(existTag), 1, checkedObj)  // 存在替换
      } else {
        this.checkedIdArr.push(checkedObj)  // 不存在添加
      }
    },
    // 点击tag
    clickTag(tag, num) {
      // console.log(tag, num)
      // 重置点击样式
      let info = this.comboxes.filter(item => item.num === num)[0]
      info.list.forEach(el => {
        el.typeColor = el.departmentStatus==='1'?'':'info'
        el.checked = false
      })
      // tag.checked = true
      // tag.typeColor = 'success'
      this.$set(tag, 'checked', true)
      this.$set(tag, 'typeColor', 'success')
      this.checkedId = tag.id
      // 处理渲染页面的数据
      this.getChildrenList(num, tag.id, tag)
      this.checkedTagList(num, tag)
    },
    // 添加按钮
    addDepartment(item) {
      this.formValidate.departmentName = ''
      this.num = item.num
      // console.log(item)
      this.modalShow = true
      this.modify = false
      this.formValidate.parentId = item.id
      const obj = this.allList.filter(el => el.id === item.id)
      this.parentName = obj.length !== 0 ? obj[0].departmentName : '无'
    },
    // 修改按钮
    modifyDepartment(item) {
      // console.log(item)
      this.num = item.num
      let checkedEditor = item.list.filter(el => el.checked)
      // console.log(checkedEditor)
      if(checkedEditor.length === 0){
        this.$Message.error('请选择需要修改的部门')
        return
      }
      this.modalShow = true
      this.modify = true
      this.formValidate = {
        departmentName: checkedEditor[0].departmentName,
        departmentStatus: Number(checkedEditor[0].departmentStatus),
        parentId: checkedEditor[0].parentId,
        id: checkedEditor[0].id
      }
      const obj = this.allList.filter(el => el.id === item.id)
      this.parentName = obj.length !== 0 ? obj[0].departmentName : '无'
    },
    // 确认提交
    handleSubmit(name) {
      this.$refs[name].validate(async (valid) => {
        if (valid) {
          const res = await this.$ajaxPost(this.modify ? this.projectName + '/mgmt/department/update.do' :this.projectName + '/mgmt/department/save.do', this.formValidate)
          this.$Message.success(this.modify ?'修改成功!':'新增成功!')
          this.modalShow = false
          await this.getDepartmentAllList()
          this.autoCheckedTag()
        } else {
          this.$Message.error('新增部门失败，请刷新后重新尝试');
        }
      })
    },
    autoCheckedTag() {
      this.checkedIdArr.forEach(el => {
        this.clickTag(el.tag, el.line)
      })
    },
    // 取消添加
    handleReset (name) {
      this.$refs[name].resetFields()
      this.modalShow = false
    }
  }
}
</script>

<style lang="scss" scoped>
.department-manage{
  .el-card{
    margin-bottom: 15px;
    .el-tag{
      cursor: pointer;
      margin-right: 10px;
      user-select: none;
    }
  }
  
}
</style>




